* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

button {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #8492f6;
  border-radius: .6em;
  margin: 20px;
  padding: 1.2em 2.8em;
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  text-decoration: none;
  transition: all .15s ease-in-out;
  box-shadow: inset 0 0 40px 40px #8492f6, 0 0 #8492f6;
}

button:hover {
  box-shadow: inset 0 0 10px #8492f6, 0 0 10px 4px #8492f6;
}

button[disabled] {
  color: #000;
  cursor: not-allowed;
  background-color: rgba(0, 0, 0, 0);
  box-shadow: inset 0 0 40px 40px #d3d3d3, 0 0 #d3d3d3;
}

.container {
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
}

